import { getCustomerByID, updateCustomer } from './API.js';
import { validateIfFormFilled, showAlert } from './function.js';
( () => {

    const nameInput = document.querySelector( '#name' );
    const emailInput = document.querySelector( '#email' );
    const telInput = document.querySelector( '#tel' );
    const companyInput = document.querySelector( '#company' );
    const idInput = document.querySelector( '#id' );

    document.addEventListener( 'DOMContentLoaded', async () => {
        const URLparams = new URLSearchParams( window.location.search );

        const customerId = parseInt( URLparams.get( 'id' ) );
        const customer = await getCustomerByID( customerId );

        showCustomer( customer );

        const form = document.querySelector( '#formulario' )
        form.addEventListener( 'submit', validateCustomer );
    } );

    function showCustomer( customer ) {
        const { name, company, email, tel, id } = customer;

        nameInput.value = name;
        emailInput.value = email;
        telInput.value = tel;
        companyInput.value = company;
        idInput.value = id;
    }
    function validateCustomer( e ) {
        e.preventDefault();

        const customer = {
            name: nameInput.value,
            email: emailInput.value,
            tel: telInput.value,
            company: companyInput.value,
            id: parseInt( idInput.value ),
        }


        if ( !validateIfFormFilled( customer ) ) {
            showAlert( 'All inputs are mandatory.' );
            return;
        }

        updateCustomer( customer );

    }


} )();